import { ActionTree, GetterTree, MutationTree} from "vuex";
import axios from 'axios';
import type { Business, Category, MenuItem } from "@/interfaces";


const axiosOptions = { 
    withCredentials: false, 
    timeout: 1000
}

interface RootState {
    storeModule: StoreState;
}

interface StoreState {
    business: Business | null;
    categories: Category[] | null;
    items: MenuItem[] | null;
}
  
const state = (): StoreState => ({
    business: null,
    categories: null,
    items: null,
});
  
const getters: GetterTree<StoreState, RootState> = {
    business: (state) => state.business,
    categories: (state) => state.categories,
    items: (state) => state.items,
  };
  
  
const mutations: MutationTree<StoreState> = {
    SET_BUSINESS(state, business) {
      state.business = business;
    },
    SET_CATEGORIES(state, categories) {
      state.categories = categories;
    },
    SET_ITEMS(state, items) {
      state.items = items;
    },
  };
  
const actions: ActionTree<StoreState, RootState> = {
    async fetchStore({ commit }, storeId: string) {
        try {
            const response = await axios.get(`https://hackemy.github.io/livemenu/data/${storeId}.json`, axiosOptions);
            commit('SET_BUSINESS', response.data.business);
            commit('SET_CATEGORIES', response.data.categories);
            commit('SET_ITEMS', response.data.items);
        } catch (error) {
            throw new Error(`API Error: ${error}`);
        }
    },
};
  
export default {
    state,
    getters,
    actions,
    mutations,
  };
