import { createRouter, createWebHashHistory } from 'vue-router'
import StoreLocator from '../views/StoreLocator.vue'
import StoreView from '../views/StoreView.vue'
import AppErrors from '../views/AppErrors.vue'

const routes = [
  { path: '/', name: 'StoreLocator', component: StoreLocator },
  { path: '/store/:storeId', name: 'StoreView', props: true, component: StoreView },
  { path: '/error/:code', name: 'AppErrors', props: true, component: AppErrors },
  { path: '/*', component: AppErrors }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
